*, *::before, *::after {
  box-sizing: border-box !important; }

[class*='ratio-'] {
  width: 100%;
  position: relative; }
  [class*='ratio-']::before, [class*='ratio-']::after {
    content: ''; }
  [class*='ratio-']::before {
    display: block;
    float: left; }
  [class*='ratio-']::after {
    display: table;
    clear: both; }
  [class*='ratio-'] img.img-responsive, [class*='ratio-'] .wrapper {
    height: 100%;
    width: 100%; }
  [class*='ratio-'] > img.img-responsive, [class*='ratio-'] > .wrapper {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0; }
  [class*='ratio-'] > .wrapper {
    overflow: hidden; }
  [class*='ratio-'] .dark-infobar {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: 32px;
    background-color: rgba(0, 0, 0, 0.6);
    padding: 6px;
    color: #ffffff; }
  [class*='ratio-'].selectable .check-mark {
    opacity: 0;
    position: absolute;
    bottom: 5px;
    left: 5px;
    color: white; }
  [class*='ratio-'].selectable.selected .check-mark {
    opacity: 1; }

.ratio-500-333::before {
  padding-bottom: 66.6%; }

.ratio-500-500::before {
  padding-bottom: 100%; }

.ratio-1024-768::before {
  padding-bottom: 75%; }

.ratio-1920-850::before {
  padding-bottom: 44.27083%; }

